"use client"

import { useState, useEffect, useRef } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { 
  Search, 
  Settings, 
  Network, 
  ZoomIn, 
  ZoomOut, 
  Maximize2, 
  Eye, 
  EyeOff,
  Loader2,
  Sparkles
} from "lucide-react"
import { PRESET_MODELS } from "@/lib/model-config"

interface NetworkNode {
  id: string
  label: string
  type: "concept" | "entity" | "method"
  x: number
  y: number
  vx?: number
  vy?: number
  fx?: number | null
  fy?: number | null
  color?: string
  radius?: number
}

interface NetworkLink {
  id: string
  source: string
  target: string
  label?: string
  type: string
}

interface NetworkData {
  nodes: NetworkNode[]
  links: NetworkLink[]
  metadata?: {
    title?: string
    description?: string
    processingTime?: number
    nodeCount?: number
    linkCount?: number
  }
}

export default function KnowledgeNetworkPage() {
  const [searchQuery, setSearchQuery] = useState("")
  const [selectedModel, setSelectedModel] = useState(PRESET_MODELS[0].id)
  const [isGenerating, setIsGenerating] = useState(false)
  const [networkData, setNetworkData] = useState<NetworkData | null>(null)
  const [showLabels, setShowLabels] = useState(true)
  const [zoom, setZoom] = useState(1)
  const [offset, setOffset] = useState({ x: 0, y: 0 })
  const [isDragging, setIsDragging] = useState(false)
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 })
  const [showSettings, setShowSettings] = useState(false)
  
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const animationRef = useRef<number | null>(null)
  const containerRef = useRef<HTMLDivElement>(null)

  const handleGenerateNetwork = async () => {
    if (!searchQuery.trim()) return
    
    setIsGenerating(true)
    
    try {
      const response = await fetch('/api/generate-network', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          query: searchQuery,
          modelId: selectedModel
        })
      })

      if (!response.ok) {
        throw new Error(`Network generation failed: ${response.statusText}`)
      }

      const resultData = await response.json()
      const result = resultData.data

      if (result.nodes && result.links) {
        const processedData: NetworkData = {
          nodes: result.nodes.map((node: any, index: number) => ({
            ...node,
            id: node.id || `node-${index}`,
            x: Math.random() * 800 + 100,
            y: Math.random() * 600 + 100,
            vx: 0,
            vy: 0,
            color: node.type === "concept" ? "#3b82f6" : 
                   node.type === "entity" ? "#10b981" : 
                   node.type === "method" ? "#8b5cf6" : "#6366f1",
            radius: node.type === "concept" ? 35 : node.type === "entity" ? 25 : 20
          })),
          links: result.links.map((link: any) => ({
            ...link,
            id: `link-${link.source}-${link.target}`
          })),
          metadata: {
            title: `${searchQuery.substring(0, 50)}${searchQuery.length > 50 ? "..." : ""} Knowledge Network`,
            description: `Generated network with ${result.nodes.length} nodes and ${result.links.length} connections`,
            processingTime: Date.now() - Date.now() + 3000
          }
        }
        
        setNetworkData(processedData)
      }
    } catch (error) {
      console.error("Network generation failed:", error)
    } finally {
      setIsGenerating(false)
    }
  }

  const handleZoomIn = () => {
    setZoom(prev => Math.min(prev * 1.2, 3))
  }

  const handleZoomOut = () => {
    setZoom(prev => Math.max(prev / 1.2, 0.3))
  }

  const handleResetView = () => {
    setZoom(1)
    setOffset({ x: 0, y: 0 })
  }

  const handleMouseDown = (e: React.MouseEvent) => {
    setIsDragging(true)
    setDragStart({ x: e.clientX - offset.x, y: e.clientY - offset.y })
  }

  const handleMouseMove = (e: React.MouseEvent) => {
    if (isDragging) {
      setOffset({
        x: e.clientX - dragStart.x,
        y: e.clientY - dragStart.y
      })
    }
  }

  const handleMouseUp = () => {
    setIsDragging(false)
  }

  // Network visualization effect
  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas || !networkData) return

    const ctx = canvas.getContext("2d")
    if (!ctx) return

    const resizeCanvas = () => {
      const container = canvas.parentElement
      if (container) {
        canvas.width = container.clientWidth
        canvas.height = container.clientHeight
      }
    }

    resizeCanvas()
    window.addEventListener("resize", resizeCanvas)

    let time = 0
    const animate = () => {
      time += 0.01
      
      // Clear canvas
      ctx.fillStyle = "#0a0a0a"
      ctx.fillRect(0, 0, canvas.width, canvas.height)

      // Save context state
      ctx.save()
      
      // Apply zoom and pan
      ctx.translate(canvas.width / 2, canvas.height / 2)
      ctx.scale(zoom, zoom)
      ctx.translate(-canvas.width / 2 + offset.x, -canvas.height / 2 + offset.y)

      // Update node positions with physics
      networkData.nodes.forEach((node, i) => {
        if (node.fx !== undefined && node.fx !== null && node.fy !== undefined && node.fy !== null) {
          node.x += (node.fx - node.x) * 0.05
          node.y += (node.fy - node.y) * 0.05
        }
        
        // Add some organic movement
        node.x += Math.sin(time + i) * 0.5
        node.y += Math.cos(time + i) * 0.3
      })

      // Draw links
      networkData.links.forEach((link) => {
        const sourceNode = networkData.nodes.find((n) => n.id === link.source)
        const targetNode = networkData.nodes.find((n) => n.id === link.target)
        
        if (sourceNode && targetNode) {
          ctx.beginPath()
          ctx.moveTo(sourceNode.x, sourceNode.y)
          
          // Calculate control points for curved links
          const dx = targetNode.x - sourceNode.x
          const dy = targetNode.y - sourceNode.y
          const cx = sourceNode.x + dx / 2
          const cy = sourceNode.y + dy / 2
          
          // Draw curved line
          ctx.quadraticCurveTo(cx, cy, targetNode.x, targetNode.y)
          
          ctx.strokeStyle = "rgba(148, 163, 184, 0.3)"
          ctx.lineWidth = 2
          ctx.stroke()
        }
      })

      // Draw nodes
      networkData.nodes.forEach((node) => {
        // Node shadow
        ctx.shadowColor = node.color ? `${node.color}40` : "rgba(0, 0, 0, 0.2)"
        ctx.shadowBlur = 10
        
        // Draw node
        ctx.beginPath()
        ctx.arc(node.x, node.y, node.radius || 25, 0, 2 * Math.PI)
        
        if (node.color) {
          ctx.fillStyle = node.color
        } else {
          const gradient = ctx.createRadialGradient(
            node.x, node.y, 0,
            node.x, node.y, node.radius || 25
          )
          gradient.addColorStop(0, node.color || "#6366f1")
          gradient.addColorStop(1, `${node.color || "#6366f1"}80`)
          ctx.fillStyle = gradient
        }
        
        ctx.fill()
        
        // Reset shadow for next node
        ctx.shadowColor = "transparent"
        ctx.shadowBlur = 0

        // Draw labels if enabled
        if (showLabels && node.label) {
          ctx.shadowColor = "rgba(0, 0, 0, 0.5)"
          ctx.shadowBlur = 3
          ctx.fillStyle = "#ffffff"
          ctx.font = "12px Inter"
          ctx.textAlign = "center"
          ctx.textBaseline = "middle"
          
          // Background for text
          const textMetrics = ctx.measureText(node.label)
          const padding = 6
          const bgX = node.x - textMetrics.width / 2 - padding
          const bgY = node.y + (node.radius || 25) + 8
          const bgWidth = textMetrics.width + padding * 2
          const bgHeight = 20
          
          ctx.fillStyle = "rgba(0, 0, 0, 0.8)"
          ctx.fillRect(bgX - 2, bgY - 10, bgWidth, bgHeight)
          
          ctx.fillStyle = "#ffffff"
          ctx.fillText(node.label, node.x, node.y + (node.radius || 25))
        }
      })

      // Restore context state
      ctx.restore()

      animationRef.current = requestAnimationFrame(animate)
    }

    animate()

    return () => {
      window.removeEventListener("resize", resizeCanvas)
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current)
      }
    }
  }, [networkData, showLabels, zoom, offset])

  return (
    <div className="relative w-full h-screen bg-black overflow-hidden">
      {/* Top Search Bar */}
      <div className="absolute top-4 left-4 z-10 flex items-center gap-2 bg-white/90 backdrop-blur-sm rounded-lg shadow-lg p-2">
        <div className="flex items-center gap-2">
          <Search className="w-4 h-4 text-gray-500" />
          <Input
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            placeholder="输入知识网络查询..."
            className="w-80 bg-transparent border-none focus:ring-0 text-sm"
            onKeyPress={(e) => {
              if (e.key === 'Enter') {
                handleGenerateNetwork()
              }
            }}
          />
        </div>
        
        <Select value={selectedModel} onValueChange={setSelectedModel} disabled={isGenerating}>
          <SelectTrigger className="w-40 h-8 text-xs bg-transparent border-none focus:ring-0">
            <SelectValue />
          </SelectTrigger>
          <SelectContent>
            {PRESET_MODELS.map((model) => (
              <SelectItem key={model.id} value={model.id} className="text-xs">
                {model.name}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
        
        <Button
          onClick={handleGenerateNetwork}
          disabled={isGenerating || !searchQuery.trim()}
          size="sm"
          className="h-8 bg-blue-600 hover:bg-blue-700"
        >
          {isGenerating ? (
            <Loader2 className="w-4 h-4 animate-spin" />
          ) : (
            <Sparkles className="w-4 h-4" />
          )}
        </Button>
      </div>

      {/* Network Info */}
      {networkData && (
        <div className="absolute top-4 right-4 z-10 bg-white/90 backdrop-blur-sm rounded-lg shadow-lg p-3">
          <div className="flex items-center gap-2 mb-2">
            <Network className="w-4 h-4 text-blue-600" />
            <span className="text-sm font-medium">
              {networkData.metadata?.title || "Knowledge Network"}
            </span>
          </div>
          <div className="flex gap-2">
            <Badge variant="secondary" className="text-xs">
              {networkData.nodes.length} 节点
            </Badge>
            <Badge variant="secondary" className="text-xs">
              {networkData.links.length} 连接
            </Badge>
          </div>
        </div>
      )}

      {/* Control Panel */}
      <div className="absolute bottom-4 right-4 z-10 flex flex-col gap-2">
        <div className="bg-white/90 backdrop-blur-sm rounded-lg shadow-lg p-1">
          <Button
            variant="ghost"
            size="icon"
            onClick={handleZoomIn}
            className="h-8 w-8"
          >
            <ZoomIn className="w-4 h-4" />
          </Button>
          <Button
            variant="ghost"
            size="icon"
            onClick={handleZoomOut}
            className="h-8 w-8"
          >
            <ZoomOut className="w-4 h-4" />
          </Button>
          <Button
            variant="ghost"
            size="icon"
            onClick={handleResetView}
            className="h-8 w-8"
          >
            <Maximize2 className="w-4 h-4" />
          </Button>
        </div>
        
        <div className="bg-white/90 backdrop-blur-sm rounded-lg shadow-lg p-1">
          <Button
            variant="ghost"
            size="icon"
            onClick={() => setShowLabels(!showLabels)}
            className="h-8 w-8"
          >
            {showLabels ? <Eye className="w-4 h-4" /> : <EyeOff className="w-4 h-4" />}
          </Button>
          <Button
            variant="ghost"
            size="icon"
            onClick={() => setShowSettings(!showSettings)}
            className="h-8 w-8"
          >
            <Settings className="w-4 h-4" />
          </Button>
        </div>
      </div>

      {/* Settings Panel */}
      {showSettings && (
        <div className="absolute top-16 left-4 z-10 bg-white/90 backdrop-blur-sm rounded-lg shadow-lg p-4 w-80">
          <h3 className="text-sm font-medium mb-3">网络设置</h3>
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <span className="text-xs">节点动画</span>
              <input type="checkbox" defaultChecked className="rounded" />
            </div>
            <div className="flex items-center justify-between">
              <span className="text-xs">连接动画</span>
              <input type="checkbox" defaultChecked className="rounded" />
            </div>
            <div className="flex items-center justify-between">
              <span className="text-xs">显示粒子效果</span>
              <input type="checkbox" defaultChecked className="rounded" />
            </div>
            <div className="flex items-center justify-between">
              <span className="text-xs">自动布局</span>
              <input type="checkbox" defaultChecked className="rounded" />
            </div>
          </div>
        </div>
      )}

      {/* Network Canvas */}
      <div 
        ref={containerRef}
        className="w-full h-full cursor-move"
        onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onMouseLeave={handleMouseUp}
      >
        <canvas
          ref={canvasRef}
          className="w-full h-full"
        />
      </div>

      {/* Welcome State */}
      {!networkData && !isGenerating && (
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="text-center">
            <Network className="w-16 h-16 text-gray-600 mx-auto mb-4" />
            <h2 className="text-2xl font-bold text-white mb-2">知识网络引擎</h2>
            <p className="text-gray-400 mb-6">
              输入查询以生成因果知识网络
            </p>
            <div className="text-sm text-gray-500">
              <p>例如: "机器学习与模型性能的因果关系"</p>
              <p>或: "人工智能在医疗领域的应用"</p>
            </div>
          </div>
        </div>
      )}

      {/* Loading State */}
      {isGenerating && (
        <div className="absolute inset-0 flex items-center justify-center bg-black/50">
          <div className="text-center">
            <Loader2 className="w-12 h-12 text-blue-500 mx-auto mb-4 animate-spin" />
            <h3 className="text-lg font-medium text-white mb-2">正在生成知识网络</h3>
            <p className="text-gray-400">
              正在分析查询并构建网络结构...
            </p>
          </div>
        </div>
      )}
    </div>
  )
}
